
.contact-tab {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contact-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 10px;
}

.contact-card {
  border: 1px solid var(--sb-border);
  border-radius: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.8rem;
}

.contact-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 6px;
}

.contact-name {
  font-weight: 600;
}

.contact-tag {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 0.7rem;
  color: var(--sb-muted);
}

.contact-relation {
  color: var(--sb-muted);
}

.contact-meta {
  display: flex;
  flex-direction: column;
  gap: 2px;
  color: var(--sb-muted);
}

.contact-summary {
  font-size: 0.75rem;
  color: var(--sb-text);
}
