
.health-tab {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.header-card {
  border: 1px solid var(--sb-border);
  border-radius: 14px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.header-title {
  font-weight: 600;
  font-size: 1rem;
}

.header-sub {
  font-size: 0.8rem;
  color: var(--sb-muted);
}

.section {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.section-title {
  font-size: 0.9rem;
  font-weight: 600;
}

.metric-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 10px;
}

.metric-card {
  border: 1px solid var(--sb-border);
  border-radius: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.8rem;
}

.metric-card.wide {
  grid-column: span 2;
}

.metric-label {
  color: var(--sb-muted);
}

.metric-value {
  font-weight: 600;
}

.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.tag-chip {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 0.72rem;
}

.analysis-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.analysis-item {
  border: 1px solid var(--sb-border);
  border-radius: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.analysis-name {
  font-weight: 600;
}

.analysis-value {
  font-size: 0.9rem;
}

.analysis-note {
  color: var(--sb-muted);
  font-size: 0.75rem;
}

.progress-bar {
  height: 6px;
  border-radius: 999px;
  background: var(--sb-border);
  overflow: hidden;
}

.progress-bar span {
  display: block;
  height: 100%;
  background: var(--sb-accent);
}

.muted {
  color: var(--sb-muted);
}
