
.status-shell {
  width: 100%;
  max-width: 760px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: var(--sb-text);
  font-family: var(--sb-font);
}

.status-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
  border: 1px solid var(--sb-border);
  border-radius: 14px;
  padding: 10px 12px;
  cursor: pointer;
}

.status-left {
  min-width: 120px;
}

.time {
  font-size: 1.2rem;
  font-weight: 600;
}

.date {
  color: var(--sb-muted);
  font-size: 0.85rem;
}

.status-middle {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
  flex: 1;
}

.icon-group {
  display: flex;
  align-items: center;
  gap: 6px;
  border: 1px solid var(--sb-border);
  padding: 4px 8px;
  border-radius: 999px;
  font-size: 0.75rem;
}

.icon-group.battery {
  gap: 8px;
}

.icon-label {
  color: var(--sb-muted);
}

.icon-bars {
  display: flex;
  gap: 3px;
}

.bar {
  width: 5px;
  height: 10px;
  border-radius: 2px;
  background: var(--sb-border);
}

.bar.active {
  background: var(--sb-accent);
}

.battery-shell {
  width: 40px;
  height: 8px;
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  overflow: hidden;
}

.battery-fill {
  display: block;
  height: 100%;
  background: var(--sb-accent);
}

.battery-text {
  font-size: 0.75rem;
  color: var(--sb-muted);
}

.status-right {
  min-width: 160px;
}

.notice-header {
  font-size: 0.78rem;
  color: var(--sb-muted);
  margin-bottom: 4px;
}

.notice-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.notice-item {
  display: flex;
  gap: 6px;
  font-size: 0.78rem;
  align-items: baseline;
}

.notice-name {
  font-weight: 600;
}

.notice-snippet {
  color: var(--sb-muted);
}

.toggle-button {
  margin-left: auto;
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 4px 12px;
  background: transparent;
  font-size: 0.75rem;
  color: var(--sb-muted);
}

.panel {
  border: 1px solid var(--sb-border);
  border-radius: 16px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.panel-title {
  font-size: 1rem;
  font-weight: 600;
}

.panel-subtitle {
  font-size: 0.82rem;
  color: var(--sb-muted);
}

.ghost-button {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 4px 10px;
  background: transparent;
  font-size: 0.75rem;
  color: var(--sb-muted);
}

.tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.tab-button {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 4px 12px;
  background: transparent;
  font-size: 0.8rem;
  color: var(--sb-muted);
}

.tab-button.primary {
  color: var(--sb-accent);
  border-color: var(--sb-accent);
}

.tab-button.active {
  background: var(--sb-accent);
  color: #fff;
  border-color: var(--sb-accent);
}

.tab-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.panel-section {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.section-title {
  font-weight: 600;
  font-size: 0.9rem;
}

.metric-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 10px;
}

.metric-card {
  border: 1px solid var(--sb-border);
  border-radius: 12px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.8rem;
}

.metric-card.wide {
  grid-column: span 2;
}

.metric-label {
  color: var(--sb-muted);
}

.metric-value {
  font-weight: 600;
}

.progress-bar {
  height: 6px;
  border-radius: 999px;
  background: var(--sb-border);
  overflow: hidden;
}

.progress-bar span {
  display: block;
  height: 100%;
  background: var(--sb-accent);
}

.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.tag-chip {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 0.75rem;
}

.muted {
  color: var(--sb-muted);
}
