
.tree {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.tree-item {
  border-left: 1px dashed var(--sb-border);
  padding-left: 8px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  align-items: baseline;
  font-size: 0.8rem;
}

.key {
  font-weight: 600;
}

.value {
  color: var(--sb-muted);
}

.array-list {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  padding-left: 6px;
}

.chip {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 2px 6px;
  font-size: 0.72rem;
}

.muted {
  color: var(--sb-muted);
  font-size: 0.72rem;
}
