
.news-tab {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.ticker {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  overflow: hidden;
  padding: 4px 8px;
}

.ticker-track {
  display: inline-flex;
  gap: 20px;
  animation: ticker 16s linear infinite;
  white-space: nowrap;
}

.ticker-item {
  font-size: 0.78rem;
  color: var(--sb-muted);
}

.banner {
  border: 1px solid var(--sb-border);
  border-radius: 14px;
  padding: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 6px;
}

.banner-title {
  font-size: 1.1rem;
  font-weight: 600;
}

.banner-subtitle {
  font-size: 0.8rem;
  color: var(--sb-muted);
}

.category-row {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.category-chip {
  border: 1px solid var(--sb-border);
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 0.72rem;
  color: var(--sb-muted);
}

.news-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.news-card {
  border: 1px solid var(--sb-border);
  border-radius: 14px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.news-header {
  display: flex;
  justify-content: space-between;
  font-size: 0.75rem;
  color: var(--sb-muted);
}

.news-category {
  font-weight: 600;
}

.news-title {
  font-weight: 600;
  font-size: 0.95rem;
}

.news-body {
  font-size: 0.82rem;
  color: var(--sb-muted);
}

.comment-box {
  border-top: 1px dashed var(--sb-border);
  padding-top: 6px;
  font-size: 0.78rem;
  color: var(--sb-muted);
}

.comment-box summary {
  cursor: pointer;
}

.comment-list {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 6px;
}

.comment-item {
  border: 1px solid var(--sb-border);
  border-radius: 10px;
  padding: 6px 8px;
  background: transparent;
}

.comment-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.7rem;
  color: var(--sb-muted);
}

.comment-user {
  font-weight: 600;
}

.comment-text {
  color: var(--sb-text);
  margin-top: 2px;
}

@keyframes ticker {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-50%);
  }
}

@media (prefers-reduced-motion: reduce) {
  .ticker-track {
    animation: none;
  }
}
